<template>
  <div class="home">
    <h1>전화번호부</h1>
    <h2>리스트</h2>
    <p>등록된 전화번호 리스트입니다</p>
    <table border="1ps solid #000000">
      <thead>
        <tr>
          <th>이름(name)</th>
          <th>핸드폰(hp)</th>
          <th>회사(company)</th>
          <th>관리</th>
        </tr>
      </thead>
      <tbody v-bind:key="i" v-for="(v,i) in data">
        <tr>
          <td>{{ v.name }}</td>
          <td>{{v.hp}}</td>
          <td>{{v.company}}</td>
          <td><button>삭제하기</button> <a href="">[수정폼으로 이동]</a></td>
        </tr>
      </tbody>
    </table>
    <a href="">등록폼으로 이동</a>

  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'ListView',
  components: {
  },
  data() {
    return {
      data: []
    }
  },
  methods: {
    list() {
      axios({
        method: 'get', // put, post, delete 
        url: `${this.$store.state.apiBaseUrl}/api/list`,
        headers: { "Content-Type": "application/json; charset=utf-8" }, //전송타입
        //params: guestbookVo, //get방식 파라미터로 값이 전달
        //data: guestbookVo, //put, post, delete 방식 자동으로 JSON으로 변환 전달
        responseType: 'json' //수신타입
      }).then(response => {
        console.log(response.data); //수신데이타
        this.data = response.data
        console.log(this.data)
      }).catch(error => {
        console.log(error);
      });

    }
  },
  created(){
    this.list()
  }
}
</script>
